.contact-area {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 300px;
  background: url('data:image/png;base64,') no-repeat 50% 50%/auto 100%;
  text-align: center;

  &__title {
    margin-bottom: 0;
    font-size:30px;
    font-weight:bold;
    line-height:1;
  }

  &__desc {
    margin: 20px 0 0;
    font-size: 18px;
    line-height: 1;
  }

  &__button {
    margin-top: 20px;

    &:hover {
      background: #F7931E;
      border-color: #F7931E;
      color: #FFFFFF;
    }
  }
}
