.float-button {
  height: 48px;
  padding: 0;
  background:#FFFFFF;
  box-shadow:0px 0px 4px 0px rgba(153,153,153,0.2);
  border-radius:4px;
  border: none;
  color: #666666;

  &__text {
    font-size:14px;
    font-weight:400;
    line-height: 1;
  }
  
  &__contact {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    width: auto;
    padding: 0 12px;

    .float-button {
      &__icon {
        font-size: 24px;
      }

      &__text {
        display: none;
        margin-right: 12px;
      }
    }
  }

  &__top {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 48px;

    .float-button {
      &__icon {
        font-size: 14px;
        transform: rotate(180deg);
      }

      &__text {
        margin-top: 2px;
        font-weight: bold;
      }
    }
  }

  &:hover {
    background: #2661E9;
    color:#FFFFFF;

    .float-button {
      &__contact {
        .float-button {
          &__text {
            display: block;
          }
        }
      }
    }
  }

  &.focus,
  &:focus {
    box-shadow: none;
  }

  & + & {
    margin-top: 5px;
  }
}
