
@import '../../common/mixin';
@import '../../components/contactArea.scss';
@import '../../components/floatButton.scss';


.study {
  &__subject {
    background:#FFFFFF;
    box-shadow:0 16px 16px 0 rgba(153,153,153,0.08);

    &-main {
      justify-content: center;
    }

    &-item {
      position: relative;
      padding: 40px 0;
      background: #F9F9F9;
      text-align: center;
      cursor: pointer;

      &:hover,
      &.active {
        background: #EBF2FA;
        text-decoration: none;
      }

      &.active {
        &:after {
          content: '';
          position: absolute;
          left: 50%;
          bottom: 0;
          transform: translateX(-50%);
          width:100px;
          height:4px;
          background: #2661E9;
        }
        .study__subject {
          &-title {
            color: #2661E9;
          }
        }
      }
    }

    &-icon {
      width: 36px;
      height: 36px;
      object-fit: contain;
    }

    &-title {
      margin: 20px 0 0;
      font-weight: bold;
      color: #333333;
    }
  }

  &__course {
    padding: 80px 15px 110px;

    &-item {
      & + & {
        margin-top: 60px;
      }
    }

    &-title {
      position: relative;
      margin: 0 -15px;
      margin-bottom: 0;
      padding: 23px 13px;
      font-weight: 400;
      color: #333333;
      line-height: 1;

      &:before {
        content: '';
        position: absolute;
        top: 50%;
        left: 0;
        transform: translateY(-50%);
        display: inline-block;
        width:4px;
        height:20px;
        background: #2661E9;
      }
    }

    &-info {
      position: relative;
      width:600px;
      height:337px;
      padding: 0;
      background:#BABABA;
    }

    &-cover {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    &-bottom {
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      padding: 16px 30px;
      background: rgba($color: #000000, $alpha: .5);
    }

    &-intro {
      margin-bottom: 0;
      font-size:14px;
      font-weight:400;
      line-height: 1;
      color:#FFFFFF;
      @include multi-text-ellipsis;
    }
  }

  &__video {
    height:337px;
    margin-bottom: 0;
    padding: 0;
    background:#FFFFFF;
    border:1px solid #DCDCDC;
    overflow: auto;

    &-item {
      display: flex;
      align-items: center;
      padding: 23px 20px;

      &.hover,
      &:hover {
        padding-top: 30px;
        padding-bottom: 30px;
        background: #F1F5FA;

        .study__video {
          &-index,
          &-title {
            font-weight: bold;
          }

          &-main {
            display: block;
            flex: 1;
          }

          &-time {
            margin-top: 12px;
            margin-left: 0;
          }

          &-button {
            display: block;
          }
        }
      }

      & + & {
        margin-top: 4px;
      }
    }

    &-index {
      font-size:16px;
      font-weight:400;
    }

    &-main {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 100%;
      padding: 0 20px;
      overflow: hidden;
    }

    &-title {
      margin-bottom: 0;
      font-weight: 400;
      line-height: 1.5;
      @include text-ellipsis;
    }

    &-time {
      margin-left: 20px;
      font-size:14px;
      font-weight:400;
      color:#999999;
      line-height: 1;
    }

    &-button {
      display: none;
      box-shadow: none;
    }
  }
}
